.page {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
}

.page__body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.header__picture {
  width: 40px;
  height: 40px;
  padding: 0;
}

.card {
  box-sizing: border-box;
  cursor: pointer;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s linear;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  text-align: left;
  margin-top: 4px;
  margin-bottom: 0;
  margin-left: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: flex-end;
  align-items: flex-end;
  column-gap: 17px;
  margin-top: 8px;
  margin-bottom: 8px;
  display: flex;
}

.card__review-amount {
  text-align: right;
  color: #060b35;
  justify-content: flex-end;
  align-items: flex-end;
  width: 53px;
  height: 16px;
  margin: 6px 0 10px;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
  display: flex;
}

.card__buy {
  box-sizing: border-box;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
  line-height: 40px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card .card__price {
  justify-content: space-between;
  height: 18px;
  margin: 5px 0 15px;
  display: flex;
  overflow: hidden;
}

.card .card__price-title {
  color: #616070;
  width: 31px;
  height: 18px;
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card .card__price-amount {
  text-align: right;
  color: #060b35;
  width: 50px;
  height: 18px;
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.stars {
  align-items: center;
  gap: 4px;
  margin: 8px 0 12px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  display: flex;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.catalog {
  grid-template-columns: repeat(auto-fit, 200px);
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(auto-fit, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.nav__linkstyle {
  color: #060b35;
  text-transform: uppercase;
  height: 60px;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: inline-block;
  position: relative;
}

.nav__list {
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__items {
  margin-right: 20px;
  display: inline-block;
}

.nav__items:last-child {
  margin-right: 0;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__list :nth-child(4) .nav__linkstyle:hover {
  color: #00acdc;
}
